* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
.body--scrolloff {
  overflow: hidden;
}
.main {
  position: relative;
  background-image: url('../img/main-background.jpg');
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main:after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
}
.caps {
  z-index: 1;
  position: absolute;
  opacity: 0;
  top: 50%;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  border: 1px solid #000000;
  padding: 5px 18px;
  border-radius: 25px 25px 0px 25px;
  transform: translate(-80%, -120%);
  transition: all 0.5s ease-in;
  font-size: 16px;
  text-transform: uppercase;
  color: #ffffff;
  font-weight: bold;
  text-align: left;
}
.caps--open {
  opacity: 1;
  transition: all 0.5s ease-in;
}
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/montserrat/v18/JTUSjIg1_i6t8kCHKm459Wlhzg.ttf) format('truetype');
}
@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/poppins/v15/pxiEyp8kv8JHgFVrJJfedw.ttf) format('truetype');
}
.login-form {
  z-index: 1;
  width: 370px;
  text-align: center;
  padding: 60px 32px;
  background: rgba(255, 255, 255, 0.08);
  box-shadow: -1px 4px 28px 0px rgba(0, 0, 0, 0.75);
}
@media (max-width: 479px) {
  .login-form {
    width: 100%;
  }
}
.login-form__images {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 25px;
}
.login-form__img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
  color: #ffffff;
}
.login-form__img:first-child {
  margin-right: 7px;
}
.login-form__title {
  color: #fff;
  font-size: 33px;
  font-weight: 600;
  margin: 0 0 35px 0;
  font-family: 'Poppins', sans-serif;
}
.login-form__item {
  position: relative;
  height: 45px;
  width: 100%;
  display: flex;
  background: rgba(255, 255, 255, 0.94);
  margin: 0 0 20px 0;
}
.login-form__show-password {
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.closed-icon {
  opacity: 0.6;
  background-color: transparent;
}
.closed-icon--open {
  opacity: 1;
}
.closed-eye {
  transition: all 0.5s ease-in;
  opacity: 1;
}
.closed-eye--open {
  transition: all 0.5s ease-in;
  transform: translate(-40%, -35%);
  opacity: 0;
}
.login-form__item-icon {
  position: absolute;
  left: 5px;
  top: 50%;
  transform: translateY(-50%);
  color: #222;
  width: 20px;
  height: 20px;
  line-height: 45px;
}
.login-form__input {
  height: 100%;
  width: 100%;
  padding-left: 35px;
  background: transparent;
  border: none;
  color: #222;
  font-size: 16px;
  font-family: 'Poppins', sans-serif;
  transition: all 0.4s ease-in;
}
.login-form__input--password {
  padding-right: 60px;
}
.login-form__input:hover,
.login-form__input:focus {
  transition: all 0.4s ease-in;
  box-shadow: 3px 3px 20px #d5d5d5, -3px -3px 20px #ebebeb;
}
.login-form__login-button {
  height: 45px;
  width: 100%;
  background: #3498db;
  border: 1px solid #2691d9;
  color: #fff;
  font-size: 18px;
  letter-spacing: 1px;
  font-weight: 600;
  cursor: pointer;
  font-family: 'Montserrat', sans-serif;
}
.login-form__login-button:hover {
  background: #2691d9;
}
